.button {
	display: inline-block;
	color: #eee;
	background: transparent;
	text-shadow: none;
	border: 3px solid #42afee;
	box-shadow: none;
	transform: skew(-6deg);
	padding: 10px 25px;
	font-size: 1.4rem;
	cursor: pointer;
	transition: 0.2s linear;
	will-change: transform, box-shadow;
	font-weight: 500;
  }
.button:hover {
	box-shadow: 0 12px 30px -12px black;
	transform: translateY(-3px) skew(-6deg);
}
  
